@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@300&display=swap');

$bg: #1d1d1d;

main {
    height: 100vh;
    width: 100;
    .left {
        height: 90vh;
        display: flex;
        width: calc(100vw / 2);
        padding-left: 50px;
        flex-direction: column;
        float: left;

        .top {
            margin-top: 10px;
            .body {
                margin-left: 20px;
            }
        }

        .middle {
            margin-left: 30px;
            margin-top: 150px;
            h1 {
                margin-left: 30px;
            }
        }

        .bottom {
            margin-left: 40px;
            margin-top: 100px;
            width: 200px;

            a { 
                text-decoration: none;

                div {
                    background: transparent;
                    outline: none;
                    box-shadow: none;
                    border: 1px solid #08fdd8;
                    height: 50px;
                    width: 200px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    transition: 0.5s;
                    color: #08fdd8;
                    font-family: 'Lexend';
                    letter-spacing: 1px;
                }

                div:hover {
                    background: #08fdd8;
                    color: black;
                }
            }
        }
    }
}

@media screen and (max-width: 600px) {
    main {
        .left {
            padding-left: 10px;
        }
    }
}